import React from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { ListChecks, Sparkles, Wrench } from 'lucide-react';

interface ChangeLogDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

export const ChangeLogDialog: React.FC<ChangeLogDialogProps> = ({ open, onOpenChange }) => {
  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-lg rounded-2xl">
        <DialogHeader>
          <DialogTitle className="text-right flex items-center gap-2 justify-end">
            <ListChecks className="w-5 h-5" />
            سجل التغييرات الأخيرة
          </DialogTitle>
          <DialogDescription className="text-right">
            هذه أبرز التحسينات التي تمت مؤخرًا في الواجهة.
          </DialogDescription>
        </DialogHeader>

        <div className="space-y-4">
          <div className="flex items-start gap-3">
            <Wrench className="w-5 h-5 text-amber-600 mt-1" />
            <div className="text-right">
              <div className="font-semibold">تحسين نافذة تعديل المديونية</div>
              <div className="text-sm text-muted-foreground">استبدال نافذة الإدخال القديمة (prompt) بحوار أنيق لإدخال المبلغ مع تأكيد.</div>
            </div>
          </div>
          <div className="flex items-start gap-3">
            <Sparkles className="w-5 h-5 text-violet-600 mt-1" />
            <div className="text-right">
              <div className="font-semibold">تحديث شاشة التحميل</div>
              <div className="text-sm text-muted-foreground">تغيير النص إلى "صلي علي النبي" مع خط عربي جميل ونبض لطيف.</div>
            </div>
          </div>
          <div className="flex items-start gap-3">
            <ListChecks className="w-5 h-5 text-green-600 mt-1" />
            <div className="text-right">
              <div className="font-semibold">بطاقات الأرباح وعدد العمليات</div>
              <div className="text-sm text-muted-foreground">إضافة بطاقات عرض أرباح الفترة الحالية وعدد العمليات في تفاصيل المحفظة.</div>
            </div>
          </div>
          <div className="flex items-start gap-3">
            <ListChecks className="w-5 h-5 text-blue-600 mt-1" />
            <div className="text-right">
              <div className="font-semibold">إصلاح تمرير نافذة اليومية</div>
              <div className="text-sm text-muted-foreground">منع تمرير الصفحة الخلفية عند فتح نافذة اليومية لراحة الاستخدام.</div>
            </div>
          </div>
        </div>

        <div className="flex justify-end gap-2 mt-6">
          <Button variant="secondary" onClick={() => onOpenChange(false)}>إغلاق</Button>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default ChangeLogDialog;